// Settings component for system configuration
import React, { useState, useEffect } from 'react';
import { getSystemSettings, updateSystemSettings } from '../services/database';

const Settings = ({ onClose }) => {
  const [settings, setSettings] = useState({
    defaultPlayerNames: {
      A: ' A',
      B: ' B',
      C: ' C',
      D: ' D'
    }
  });
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');

  useEffect(() => {
    loadSettings();
  }, []);

  const loadSettings = async () => {
    try {
      const systemSettings = await getSystemSettings();
      setSettings(systemSettings);
    } catch (err) {
      setError('');
      console.error('Error loading settings:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const handlePlayerNameChange = (player, name) => {
    setSettings(prev => ({
      ...prev,
      defaultPlayerNames: {
        ...prev.defaultPlayerNames,
        [player]: name
      }
    }));
  };

  const handleSave = async () => {
    setIsSaving(true);
    setError('');
    setSuccessMessage('');

    try {
      await updateSystemSettings(settings);
      setSuccessMessage('');
      setTimeout(() => {
        onClose();
      }, 1000);
    } catch (err) {
      setError('');
      console.error('Error saving settings:', err);
    } finally {
      setIsSaving(false);
    }
  };

  if (isLoading) {
    return (
      <div className="modal-overlay">
        <div className="modal-content">
          <div className="loading">...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h2></h2>
          <button className="close-btn" onClick={onClose}></button>
        </div>
        
        <div className="modal-body">
          <div className="settings-section">
            <h3></h3>
            <div className="settings-description">
              <p></p>
            </div>
            <div className="player-names-grid">
              {Object.entries(settings.defaultPlayerNames).map(([player, name]) => (
                <div key={player} className="input-group">
                  <label htmlFor={`player-${player}`}> {player}:</label>
                  <input
                    id={`player-${player}`}
                    type="text"
                    value={name}
                    onChange={(e) => handlePlayerNameChange(player, e.target.value)}
                    placeholder={` ${player}`}
                    maxLength={20}
                  />
                </div>
              ))}
            </div>
          </div>

          {error && <div className="error-message">{error}</div>}
          {successMessage && <div className="success-message">{successMessage}</div>}
        </div>

        <div className="modal-footer">
          <button 
            className="cancel-btn" 
            onClick={onClose}
            disabled={isSaving}
          >
            
          </button>
          <button 
            className="primary-btn"
            onClick={handleSave}
            disabled={isSaving}
          >
            {isSaving ? '...' : ''}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Settings;